/* Global */
body,
html,
#content {
  height: 100%;
}

/* Fonts */
.text {
  font-family: 'Open Sans', Arial, Helvetica, sans-serif;
  font-size: 14px;
  color: black;
}
.bold {
  font-weight: bold;
}
.uppercase {
  text-transform: uppercase;
  letter-spacing: 0.4px;
}

.link,
a.link:hover {
  color: skyblue;
  text-decoration: underline;
  font-weight: bold;
  cursor: pointer;
}
.pointer {
  cursor: pointer;
}
.shadow {
  box-shadow: 0 1px 4px 0 rgba(0, 0, 0, 0.1);
}
.rounded {
  border-radius: 4px;
}

/* Colors */
.red {
  color: red;
}
.grey {
  color: grey;
}
.black {
  color: black;
}
.bgred {
  background-color: red;
}
.bggrey {
  background-color: grey;
}
.bgblack {
  background-color: black;
}

